import React, { useState, useEffect } from 'react';

import FormStatus from '../types/form-status';
import { Description } from '../types';
import { Preparing, Loading, Succeeded, Failed } from './others';
import { getCapacities, submit } from '../firebase/firebase-modules';

import slimeImg from '../img/.png';
import dnaImg from '../img/DNA.png';
import magnetImg from '../img/.png';
import tankImg from '../img/.png';
import robotImg from '../img/.png';
import accessoryImg from '../img/.png';

const initialTitles: string[] = [
  "",
  "DNA",
  "",
  "",
  "",
  ""
];
const initialSchedules: string[] = [
  '10:30',
  '11:00',
  '11:30',
  '12:00',
  '12:30',
  '13:00',
  '13:30',
  '14:00',
  '14:30',
  '15:00',
  '15:30',
  '16:00'
];
const initialGrades: string[] = [
  "",
  "1",
  "2",
  "3",
  "4",
  "5",
  "6",
  "1",
  "2",
  "3",
  ""
];

const descriptions: { [key: string]: Description } = {
  "": {
    icon: slimeImg,
    targetAge: ""
  },
  "DNA": {
    icon: dnaImg,
    targetAge: "4"
  },
  "": {
    icon: magnetImg,
    targetAge: ""
  },
  "": {
    icon: tankImg,
    targetAge: ""
  },
  "": {
    icon: robotImg,
    targetAge: " "
  },
  "": {
    icon: accessoryImg,
    targetAge: "4 "
  }
};

const Form = () => {
  const [formStatus, setStatus] = useState(FormStatus.closed);

  const [courseTitle, setTitle] = useState(initialTitles[0]);
  const [courseSchedule, setSchedule] = useState(initialSchedules[0]);
  const [userGrade, setGrade] = useState(initialGrades[0]);
  const [userLastName, setLastName] = useState("");
  const [userFirstName, setFirstName] = useState("");
  const [userEmail, setEmail] = useState("");
  const [userMemo, setMemo] = useState("");
  const [capacities, setCapacities] = useState({} as { [key: string]: number });
  useEffect(() => {
    const fetchData = async () => {
      const result = await getCapacities(courseTitle);
      setCapacities(result!);
    };
    fetchData();
  }, [courseTitle]);

  const courseButton = (title: string) => {
    const description = descriptions[title];
    return <label className='ml-5 my-4'>
      <input
        type='radio'
        value={title}
        onChange={(e: React.ChangeEvent<HTMLInputElement>) => setTitle(e.target.value)}
        checked={title === courseTitle}
      />
      <div className='ml-1 -my-1.5'>
        <p className='text-base'>{title}<br /></p>
        <p className='text-sm'>: {description.targetAge}</p>
        <img src={description.icon} alt={title} className='w-full' />
      </div>
    </label>;
  };

  const scheduleButton = (schedule: string, capacity: number) => {
    const acceptable = capacity > 0;
    return <label className='ml-5 my-4' style={{ color: (capacity > 1 ? 'black' : 'red') }}>
      <input
        type='radio'
        value={schedule}
        onChange={(e: React.ChangeEvent<HTMLInputElement>) => setSchedule(e.target.value)}
        checked={courseSchedule === schedule}
        disabled={!acceptable}
      />
      <span className='ml-1 -mt-1.5'>
        {schedule}
        {acceptable ? `${capacity}` : ``}
      </span>
    </label>;
  };

  const isSubmittable = () => {
    const targets = [userLastName.length, userFirstName.length, userEmail.length, capacities[courseSchedule]];
    return targets.every((n: number) => n > 0);
  };

  const handleSubmit = async (e: React.MouseEvent<HTMLElement, MouseEvent>) => {
    e.preventDefault();
    const status = await submit(courseTitle, courseSchedule, userGrade, `${userLastName} ${userFirstName}`, userEmail, userMemo);
    setStatus(status);
  };

  const showErrorMessage = () => {
    return isSubmittable()
      ? <div className='pb-6'></div>
      : <span className='caution'>&#9888; </span>;
  };

  if (formStatus === FormStatus.closed) {
    return <Preparing />;
  }
  else if (capacities == null || initialSchedules.some((schedule) => isNaN(capacities[schedule]))) {
    return <Loading />;
  }
  else if (formStatus === FormStatus.succeeded) {
    return <Succeeded
      name={`${userLastName} ${userFirstName}`}
      grade={userGrade}
      email={userEmail}
      title={courseTitle}
      schedule={courseSchedule}
    />;
  }
  else if (formStatus === FormStatus.failed) {
    return <Failed />;
  }

  return (
    <div>
      <p className='mb-5'>
        
        <br />
        30
      </p>

      <h2 className='title'>1.  *</h2>
      {initialTitles.map(courseButton)}
      <div className='mb-10' />

      <h2 className='title'>2.  *</h2>
      {initialSchedules.map((schedule) => scheduleButton(schedule, capacities[schedule]))}
      <div className='mb-10' />

      <h2 className='title'>3.  *</h2>
      <label className='ml-5 my-3'>
        <select
          className='select-box'
          value={userGrade}
          onChange={(e: React.ChangeEvent<HTMLSelectElement>) => setGrade(e.target.value)}
        >
          {initialGrades.map((grade) => <option value={grade}>{grade}</option>)}
        </select>
      </label>
      <div className='mb-10' />

      <h2 className='title'>4.  *</h2>
      <label className='ml-5 my-3'>
        <input
          className='last-name-box'
          type='text'
          value={userLastName}
          placeholder=""
          style={{ width: '49.5%' }}
          onChange={(e: React.ChangeEvent<HTMLInputElement>) => setLastName(e.target.value)}
        />
        <input
          className='first-name-box'
          type='text'
          value={userFirstName}
          placeholder=""
          style={{ width: '49.5%' }}
          onChange={(e: React.ChangeEvent<HTMLInputElement>) => setFirstName(e.target.value)}
        />
      </label>
      <div className='mb-10' />

      <h2 className='title'>5.  *</h2>
      <label className='ml-5 my-3'>
        <input
          className='text-box'
          type='email'
          value={userEmail}
          pattern=".+@.+\..+"
          placeholder="xxxxx@example.com"
          onChange={(e: React.ChangeEvent<HTMLInputElement>) => setEmail(e.target.value)}
        />
      </label>
      <div className='mb-10' />

      <h2 className='title'>6. </h2>
      <label className='ml-5 my-3'>
        <input
          className='text-box'
          type='text'
          value={userMemo}
          onChange={(e: React.ChangeEvent<HTMLInputElement>) => setMemo(e.target.value)}
        />
      </label>
      <div className='mb-12' />

      <button
        className={isSubmittable() ? 'submit-button' : 'disabled-submit-button'}
        type='button'
        disabled={!isSubmittable()}
        onClick={handleSubmit}
      >
        
      </button>

      {showErrorMessage()}
    </div >
  );
}

export default Form;